.nav {
  grid-area: nav;
	transition: transform 0.15s;
}

.items {
  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.15);
  margin: 10px;
  padding: 10px;
}

.item {
  margin: 10px 20px 10px 20px;
}

.item a {
  text-decoration: none;
  color: rgb(0, 0, 0);
  transition: 0.2s, font-weight 0ms;
}

.item a:hover {
  text-shadow: rgb(51, 51, 51) 0px 0px 2px;
}

.item a.active {
  font-weight: 400;
  color: rgb(0, 0, 0);
  margin-left: 5px;
  text-shadow: rgb(51, 51, 51) 0px 0px 2px;
}

.nav.active {
  transform: translate(0,0);	
}

.nav.active .items{
	box-shadow: none;
}